.App {
  text-align: center;
}

.App-header {
  background-color: #282c34;
  height: 60px;
  padding: 10px;
  color: white;
}

.game {
  position: relative;
  width: auto;
  min-height: 60%;
  background-color: tomato;
  margin: 10px;
  padding: 20px;
  color: antiquewhite;
}

.random-label {
  font-size: 2rem;
  margin-left: 20px;
}

.switch input {
  display: none;
}

.switch {
  position: relative;
  display: inline-block;
  width: 60px;
  height: 34px;
  margin-bottom: -7px;
}

.slider {
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  background-color: #ccc;
  transition: .4s;
  cursor: pointer;
}

.slider:before {
  position: absolute;
  content: "";
  width: 26px;
  height: 26px;
  background-color: white;
  left: 4px;
  bottom: 4px;
  transition: .4s;
}

input:checked+.slider {
  background-color: steelblue;
}

input:focus+.slider {
  box-shadow: 0 0 10px steelblue;
}

input:checked+.slider::before {
  transform: translateX(26px);
}

.slider.round {
  border-radius: 34px;
}

.slider.round::before {
  border-radius: 50%;
}

.fields {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: stretch;
  flex: 1;
  padding: 12px
}

.field-block {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  min-height: 120px;
  font-size: 12rem;
  background-color: rgb(237, 54, 121);
  color: white;
  font-weight: 300;
  position: relative;
  margin: 5px;
  padding: 10px;
  border-radius: 4px;
}

.buttons {
  display: flex;
  flex-direction: row;
  justify-content: center;
}

.buttons a {
  text-decoration: none;
}

.button {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  flex: 0 0 auto;
  margin-left: 10px;
  margin-right: 10px;
  padding-left: 15px;
  padding-right: 15px;
  background-color: rgb(58, 218, 37);
  font-size: 1.2rem;
  height: 48px;
  color: white;
  border-radius: 4px;
  cursor: pointer
}

.sound {
  background-color: green;
}

.next {
  background-color: hsla(215, 98%, 49%, 0.788);
  padding-left: 5%;
  padding-right: 5%;
}

.prev {
  padding-left: 5%;
    padding-right: 5%;
}

.letter-image {
  width: 200px;
  height: 200px;
}

.field-block .placeholder-span {
  font-size: 1rem;
}

.field-block .word {
  font-size: 4rem;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  text-align: center;
  padding-left: 20px;
}

.hide {
  display: none !important;
}

.left-field {
  width: 45%;
  height: 100%;
}

.right-field {
  width: 45%;
  height: 100%;
}

@media only screen and (min-device-width : 320px) and (max-device-height : 480px) {
  .button {
      margin-left: 5px;
      margin-right: 5px;
    }
  
    .sound {
      background-color: green;
    }
  
    .next {
      background-color: dodgerblue;
      padding-left: 2%;
      padding-right: 2%;
    }
  
    .prev {
      padding-left: 2%;
      padding-right: 2%;
    }
  
    .letter-image {
      width: 100px;
      height: 100px;
    }
  
    .field-block .word {
      font-size: 2rem;
    }
}